import { Pipe, PipeTransform } from '@angular/core';
import { DraftAccountIdentification } from '@backbase/data-ang/payment-draft';
import { formatIBAN } from '../../helpers/iban-helpers';

@Pipe({ name: 'accountFormatter', pure: true })
export class AccountFormatterPipe implements PipeTransform {
  transform(
    identification: DraftAccountIdentification['identification'] | undefined
  ): string | undefined {
    if (identification?.schemeName === 'IBAN') {
      return formatIBAN(identification.identification);
    }
    return identification?.identification;
  }
}
